
.post-slide {
        background: #fff;
        margin: 20px 15px 20px;
        border-radius: 15px;
        padding-top: 1px;
        box-shadow: 0px 14px 22px -9px #bbcbd8;
}

.post-slide .post-img {
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        margin: -12px 15px 8px 15px;
        /* margin-left: -10px; */
}

.post-slide .post-img img {
        width: 100%;
        height: 240px;
        transform: scale(1, 1);
        transition: transform 0.2s linear;
}

.post-slide:hover .post-img img {
        transform: scale(1.1, 1.1);
}

.post-slide .over-layer {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        transition: all 0.50s linear;
}

.post-slide:hover .over-layer {
        opacity: 1;
        text-decoration: none;
}

.post-slide .over-layer i {
        position: relative;
        top: 45%;
        text-align: center;
        display: block;
        color: #fff;
        font-size: 25px;
}

.post-slide .post-content {
        background: #fff;
        padding: 2px 20px 40px;
        border-radius: 15px;
}

.post-slide .post-title a {
        font-size: 15px;
        font-weight: bold;
        color: #333;
        display: inline-block;
        text-transform: uppercase;
        transition: all 0.3s ease 0s;
}

.post-slide .post-title a:hover {
        text-decoration: none;
        color: #3498db;
}

.post-slide .post-description {
        line-height: 24px;
        color: #808080;
        margin-bottom: 25px;
}

.post-slide .post-date {
        color: #a9a9a9;
        font-size: 14px;
}

.post-slide .post-date i {
        font-size: 20px;
        margin-right: 8px;
        color: #CFDACE;
}

.post-slide .read-more {
        padding: 7px 20px;
        float: right;
        font-size: 12px;
        background: #f4733d;
        color: #ffffff;
        box-shadow: 0px 10px 20px -10px #fb9c49;
        border-radius: 25px;
        text-transform: uppercase;
}

.post-slide .read-more:hover {
        background: #3498db;
        text-decoration: none;
        color: #fff;
}



@media only screen and (max-width:1280px) {
        .post-slide .post-content {
                padding: 0px 15px 25px 15px;
        }
}

.arrow {
        position: absolute;
        border-radius: 3px;
        font-size: 50px;
        top: 35%;
}

.arrow:hover {
        /* background: #869791; */
        color: #1376c5;
}

.next {
        right: 0px;
}

.prev {
        left: 0px;
}