.card-content-4{
    height: 6rem;
    margin-top: 1rem;
}
.bookedspan{
    margin-bottom: 0.6rem;
}

.progressBorder{
    border: 0.10rem solid black;
}

.textProgress{
    text-align: center;
    color: rgb(200, 170, 170);
    z-index: 100;
}
.textcol{
    color: #1C82AD;
}

.heightdiv{
    height: 11.5rem;
    margin-top: 1rem;
    padding-top: 1rem;
    padding-left: 1.7rem;
}

.paddingInCity{
    padding-top: 1.4rem;
    padding-left: 1.4rem;
    padding-right: 1.4rem;
    padding-bottom: 0rem;
    margin-bottom: 0rem;
    font-weight: 600;
}

.paddingInName{
    padding-top: 0rem;
    margin-top: 0rem;
    padding-bottom: 0rem;
    margin-top: 0rem;
}

.prgbar{
    height: 3.5rem;
    padding: 1rem;
    padding-bottom: 0rem;
}

.distanceStyle{
    font-size: 0.6rem;
    padding-left: 1.8rem;
    margin-bottom: 0rem;
    padding-bottom: 0rem;
}

.areaText{
    margin-top: 0.4rem;
    font-size: 0.9rem;
}

.areaIcon{
    margin-right: 0.5rem;
    font-size: 1.2rem;
    color: rgb(255,102,0);
}

@media (min-width: 768px) {
    .paddingInCity{
        font-size: 0.8rem;
    }
    /* .warehouseNameStyle{
        font-size: 1rem;
    } */
}

@media (min-width: 991px) {
    .nonLaptopView{
        display: none;
    }
}

@media (max-width: 991px) {
    .nonLaptopView{
        font-size: 0.8rem;
        padding-bottom: 0.3rem;
        padding-left: 0.2rem;
    }
    .prgbar{
        height: 4.5rem;
    }
}

.realtimeUpdates{
    font-size: 0.9 rem;
    color: #44425a;
}

.updatesIcon{
    font-size: 1.2rem;
    color: black;
}